export enum FullnessLevel {
    empty = 0,
    sated = 1,
    full = 2,
    stuffed = 3,
    veryBloated = 4,
}

export function fullnessToText(fullness: number): string {
    if (fullness < 1)
        return 'empty';
    else if (fullness < 2)
        return 'sated';
    else if (fullness < 3)
        return 'full';
    else if (fullness < 4)
        return 'stuffed';
    else
        return 'very bloated';
}