import { BodyType } from "./BodyType";
import { StoryChoice } from "../story-engine/src/common/StoryChoice";

export const bodyTypeChoices = [];

for (const fieldName in BodyType) {
    const fieldValue = BodyType[fieldName];
    if (typeof fieldValue === 'number')
        bodyTypeChoices.push(new StoryChoice(fieldName, ''+ fieldValue));
}
