import { StoryScene } from "../story-engine/src/common/StoryScene";
import { LittleState } from "./LittleState";
import { heShe, hisHer, himselfHerself, himHer } from "./GenderType";

export class BaseScene extends StoryScene {
    get state(): LittleState {
        return super.getState() as LittleState;
    }
    set state(value: LittleState) {
        super.setState(value);
    }
    get heShe(): string {
        return heShe(this.state.character.gender);
    }
    get hisHer(): string {
        return hisHer(this.state.character.gender);
    }
    get himselfHerself(): string {
        return himselfHerself(this.state.character.gender);
    }
    get himHer(): string {
        return himHer(this.state.character.gender);
    }
    get characterName(): string {
        return this.state.character.name;
    }
}