import { GenderType } from "./GenderType";
import { BodyType } from "./BodyType";
import { ClothingBottom, ClothingSize, ClothingTop, clothingTopToText, clothingBottomToText } from "./clothes";
import { StoryColor, storyColorToText } from "./color";
import { getClothingSizeToBodyTypeRelation, clothingFitTypeToText, ClothingFitType } from "./clothingSizes";

export class LittleCharacter {
    name: string;
    gender: GenderType;
    bodyType: BodyType;
    strength: number;
    magic: number;
    energy: number = 0;
    fullness: number = 0;

    clothingBottom: ClothingBottom;
    clothingBottomSize: ClothingSize;
    clothingBottomColor: StoryColor;

    clothingTop: ClothingTop;
    clothingTopSize: ClothingSize;
    clothingTopColor: StoryColor;

    getClothingDescriptions(): string[] {
        const descriptions: string[] = [];
        if (this.clothingTop != null) {
            const fitType = getClothingSizeToBodyTypeRelation(this.clothingTopSize, this.bodyType);
            const description = this.name + ' wears ' + storyColorToText(this.clothingTopColor) + ' ' +
                clothingTopToText(this.clothingTop) + ', size ' + ClothingSize[this.clothingTopSize] + ' ' +
                clothingFitTypeToText(fitType);
            descriptions.push(description);
        }
        if (this.clothingBottom != null) {
            const fitType = getClothingSizeToBodyTypeRelation(this.clothingBottomSize, this.bodyType);
            const description = this.name + ' wears ' + storyColorToText(this.clothingBottomColor) + ' ' +
                clothingBottomToText(this.clothingBottom) + ', size ' + ClothingSize[this.clothingBottomSize] + ' ' +
                clothingFitTypeToText(fitType);
            descriptions.push(description);
        }
        return descriptions;
    }
    getBottomClothingShortText(): string {
        return this.clothingBottom != null ? storyColorToText(this.clothingBottomColor) + ' ' + clothingBottomToText(this.clothingBottom) : null;
    }
    getTopClothingShortText(): string {
        return this.clothingTop != null ? storyColorToText(this.clothingTopColor) + ' ' + clothingTopToText(this.clothingTop) : null;
    }
    getBottomClothingFitType(): ClothingFitType {
        return this.clothingBottom != null ? getClothingSizeToBodyTypeRelation(this.clothingBottomSize, this.bodyType) : null;
    }
    getTopClothingFitType(): ClothingFitType {
        return this.clothingTop != null ? getClothingSizeToBodyTypeRelation(this.clothingTopSize, this.bodyType) : null;
    }
    getInferredBottomClothingShortText(): string {
        if (this.clothingBottom != null)
            return this.getBottomClothingShortText();
        else
            return this.getTopClothingShortText();
    }
}