import { StoryInfo } from "./StoryInfo";
import { SceneNotFoundError } from "./SceneNotFoundError";
import { StorySceneConstructor, StoryScene } from "./StoryScene";
import { StoryState } from "./StoryState";

export type DebugCommandReceiver = (command: string, scene: StoryScene) => boolean;

export class StoryRunner {
    storyInfo: StoryInfo;
    constructor(storyInfo: StoryInfo) {
        this.storyInfo = storyInfo;
    }
    getRequiredSceneCtor(className: string): StorySceneConstructor {
        const func = this.storyInfo.scenes[className];
        if (func == null) {
            throw new SceneNotFoundError(className);
        }
        return func;
    }
    createSceneWithState(ctor: StorySceneConstructor, state: StoryState) {
        const scene = new ctor;
        scene.state = state;
        scene.state.sceneClassName = ctor['name'];
        scene.sceneMap = this.storyInfo.scenes;
        return scene;
    }
    receiveCommand: DebugCommandReceiver = function() {
        return false;
    };
    createStatusScene(currentScene: StoryScene) {
        const returningSceneName = currentScene.state.sceneClassName;
        const statusSceneCtor = this.getRequiredSceneCtor(currentScene.state.statusSceneClassName);
        const statusScene = this.createSceneWithState(statusSceneCtor, currentScene.state);
        // Avoid being locked into status scene by calling createStatusScene when currentScene is already status scene
        if (!(currentScene instanceof statusSceneCtor))
            statusScene.state.returningSceneName = returningSceneName;
        return statusScene;
    }
    createStartingScene() {
        const state = new this.storyInfo.stateConstructor;
        const ctor = this.getRequiredSceneCtor(state.sceneClassName);
        const resultScene = this.createSceneWithState(ctor, state);
        return resultScene;
    }
}