import { StoryState } from "./StoryState";
import { StoryElement } from "./StoryElement";
import { StoryText } from "./StoryText";
import { SceneMap } from "./SceneMap";
import { SceneNotFoundError } from "./SceneNotFoundError";
import { getLinearString } from "./string";

export class StoryScene {
    private _state: StoryState;
    sceneMap: SceneMap;
    nextSceneCtor: StorySceneConstructor;
    get state(): StoryState {
        return this.getState();
    };
    set state(value: StoryState) {
        this.setState(value);
    }
    getState(): StoryState {
        return this._state;
    }
    setState(value: StoryState) {
        this._state = value;
    }
    render(): StoryElement[] {
        return null;
    }
    receiveChoice(choice: string) {
    }
    text(text: string): StoryText {
        return new StoryText(getLinearString(text));
    }
    getRequiredSceneCtor(className: string) {
        if (this.sceneMap == null)
            throw new Error('StoryScene.sceneMap is null');
        const ctor = this.sceneMap[className];
        if (ctor == null)
            throw new SceneNotFoundError(className);
        return ctor;
    }
}

export type StorySceneConstructor = { new(): StoryScene };