import { BaseScene } from "../BaseScene";
import { StoryInput } from "../../story-engine/src/common/StoryInput";
import { yesNoChoices } from "../YesNoChoices";
import { StoryChoice } from "../../story-engine/src/common/StoryChoice";
import { GenderType } from "../GenderType";
import { Confirmation } from "../Confirmation";
import { getEnumMemberName, isValidEnumMember } from "../../story-engine/src/common/enum";
import { bodyTypeChoices } from "../BodyTypeChoices";
import { BodyType } from "../BodyType";
import { commonScenes } from "./CommonScenes";
import { Chars } from "../chars";
import { upperFirst } from "../../story-engine/src/common/string";
import { StoryElement } from "../../story-engine/src/common/StoryElement";

export class CharacterGenderScene extends BaseScene {
    render() {
        return [
            this.text('Choose character gender'),
            new StoryChoice('Male', GenderType.male),
            new StoryChoice('Female', GenderType.female)
        ];
    }
    receiveChoice(choice: string) {
        if (isValidEnumMember(GenderType, choice)) {
            this.state.character.gender = choice as GenderType;
            this.nextSceneCtor = CharacterGenderConfirmationScene;
        }
    }
}
commonScenes.CharacterGenderScene = CharacterGenderScene;

export class CharacterGenderConfirmationScene extends BaseScene {
    render() {
        return [
            this.text('Character gender will be: ' + getEnumMemberName(GenderType, this.state.character.gender)),
            new StoryChoice('Yes', Confirmation.yes),
            new StoryChoice('Hold on, I changed my mind...', Confirmation.no),
        ];
    }
    receiveChoice(choice: string) {
        if (choice == Confirmation.yes)
            this.nextSceneCtor = CharacterNameScene;
        else if (choice == Confirmation.no)
            this.nextSceneCtor = CharacterGenderScene;
    }
}

export class CharacterNameScene extends BaseScene {
    get defaultName(): string {
        return this.state.character.gender == GenderType.female ? 'Ayane' : 'Jake';
    }
    render() {
        return [
            this.text('Type in your character\'s name.'),
            this.text('Leave empty to get default character name: ' + this.defaultName),
            new StoryInput()
        ];
    }
    receiveChoice(choice: string) {
        if (choice == '' || choice == null)
            choice = this.defaultName;
        this.state.character.name = choice;
        this.nextSceneCtor = CharacterNameConfirmationScene;
    }
}

export class CharacterNameConfirmationScene extends BaseScene {
    render() {
        return [
            this.text('Your character\'s name is "' + this.state.character.name + '". Is that correct?'),
            ...yesNoChoices
        ];
    }
    receiveChoice(choice: string) {
        if (choice == Confirmation.yes) {
            this.nextSceneCtor = CharacterBodyTypeScene;
        } else {
            this.nextSceneCtor = CharacterNameScene;
        }
    }
}

export class CharacterBodyTypeScene extends BaseScene {
    render() {
        return [
            this.text('Choose body type for ' + this.state.character.name),
            ...bodyTypeChoices
        ]
    }
    receiveChoice(choice: string) {
        const bodyTypeIndex = parseInt(choice);
        if (isValidEnumMember(BodyType, bodyTypeIndex)) {
            this.state.character.bodyType = bodyTypeIndex as BodyType;
            this.nextSceneCtor = CharacterBodyTypeConfirmationScene;
        }
    }
}

export class CharacterBodyTypeConfirmationScene extends BaseScene {
    render() {
        let text = this.state.character.name + ' happens to be ' +
            getEnumMemberName(BodyType, this.state.character.bodyType);
        if (this.state.character.bodyType == BodyType.average)
            text += ' (neither thin nor fat)';
        text += '.';
        return [
            this.text(text),
            this.text('Is that right?'),
            ...yesNoChoices
        ]
    }
    receiveChoice(choice: string) {
        if (choice == Confirmation.yes)
            this.nextSceneCtor = CharacterAbilityChoiceScene;
        else
            this.nextSceneCtor = CharacterBodyTypeScene;
    }
}

export class CharacterAbilityChoiceScene extends BaseScene {
    render() {
        return [this.text('Choose abilities'),
            new StoryChoice('Universalist: strength 2, magic 2', '1'),
            new StoryChoice('Mage: strength 1, magic 3', '2'),
            new StoryChoice('Strong: strength 3, magic 1. Need body type: not thin', '3')
        ];
    }
    receiveChoice(choice: string) {
        this.state.character.strength = null;
        switch (choice) {
            case '1':
                this.state.character.strength = 2;
                this.state.character.magic = 2;
                break;
            case '2':
                this.state.character.strength = 1;
                this.state.character.magic = 3;
                break;
            case '3':
                if (this.state.character.bodyType != BodyType.thin) {
                    this.state.character.strength = 3;
                    this.state.character.magic = 1;
                }
                break;
        }
        if (this.state.character.strength != null)
            this.nextSceneCtor = CharacterAbilityConfirmationScene;
    }
}


export class CharacterAbilityConfirmationScene extends BaseScene {
    render() {
        let text = '';
        switch (this.state.character.strength) {
            case 1:
                text = `is not accustomed to physical labor; however ${this.hisHer} magical power is impressive. ` +
                    `${upperFirst(this.heShe)} can blast a wooden cart to pieces with a wave of ${this.hisHer} hand.`;
                break;
            case 2:
                text = `is good with both sword and magic, however lack of specialization means that ${this.heShe} ` +
                    `can neither blast a whole castle into pieces with magic nor lift a carriage off the ground with bare hands.`
                break;
            case 3:
                text = `is strong as an ox and can slice small trees and people with an axe. ` +
                    `However ${this.hisHer} magical prowess leaves something to be desired.`
                break;
        }
        text = this.state.character.name + ' ' + text;
        return [this.text(text),
            ...yesNoChoices];
    }
    receiveChoice(choice: string) {
        switch (choice) {
            case Confirmation.no:
                this.nextSceneCtor = CharacterAbilityChoiceScene;
                break;
            case Confirmation.yes:
                this.nextSceneCtor = CharacterCreationCompleteScene;
                break;
        }
    }
}

export class CharacterCreationCompleteScene extends BaseScene {
    render() {
        const elements = [
            this.text('Basic character creation process is now complete.'),
            this.text(`Hint for terminal-console version: type "status" to review character.
                Type "reset" to start from the very beginning.
                Type "help" to review list of commands.
                Type "save" or "load" at any moment to save or load the game.`)
        ];
        return elements;
    }
    receiveChoice() {
        this.nextSceneCtor = WakeUpScene;
    }
}

enum MirrorReaction {
    approve,
    neutral,
    hate
}

export class WakeUpScene extends BaseScene {
    render() {
        const elements = [this.text(`${this.state.character.name} wakes up in ${this.hisHer} room at the top floor of ${this.hisHer} wizard tower. ` +
            `The tower is tall and white. It's a new day. Yay! But it's Monday. Argh!`)];
        if (this.state.character.bodyType >= BodyType.fat)
            elements.push(this.text(`${Chars.checkMark} The wooden bed groans a little under ${this.characterName}'s weight.`));
        elements.push(this.text(`${this.state.character.name} walks up to a mirror and looks at ${this.himselfHerself}.`));
        elements.push(
            new StoryChoice('approve', '' + MirrorReaction.approve),
            new StoryChoice('neutral', '' + MirrorReaction.neutral),
            new StoryChoice('complain', '' + MirrorReaction.hate)
        );
        return elements;
    }
    receiveChoice(choice: string) {
        const reaction = parseInt(choice);
        if (isValidEnumMember(MirrorReaction, reaction)) {
            this.state.wakeUpSceneChoice = reaction;
            this.nextSceneCtor = WakeUpSceneReaction;
        }
    }
}

export class WakeUpSceneReaction extends BaseScene {
    render() {
        const elements: StoryElement[] = [];
        switch (this.state.wakeUpSceneChoice as MirrorReaction) {
            case MirrorReaction.approve:
                elements.push(this.text(`${this.state.character.name}: Looking good! Another beautiful day in a fantasy land.`));
                break;
            case MirrorReaction.neutral:
                elements.push(this.text(`${this.state.character.name} stares a little longer at ${this.hisHer} reflection but says nothing.`));
                break;
            case MirrorReaction.hate:
                elements.push(...this.complain());
                break;
        }
        elements.push(this.text('Morning sun shines through the curtain.'));
        return elements;
    }
    private complain() {
        let text = `I want to go back to sleep!`;
        if (BodyType.chubby == this.state.character.bodyType)
            text = '(' + Chars.checkMark + ' chubby) I wish I was thinner!';
        else if (BodyType.fat == this.state.character.bodyType)
            text = '(' + Chars.checkMark + ' fat) I definitely need to loose some weight.';
        else if (this.state.character.bodyType == BodyType.thin && this.state.character.gender == GenderType.female)
            text = '(' + Chars.checkMark + ' thin & female) I wish I had bigger chest!';
        else if (this.state.character.strength == 1)
            text = '(' + Chars.checkMark + ' low str) I wish I was stronger. I need to work out more.';
        return [
            this.text(this.state.character.name + ': Ugh... ' + text)
        ];
    }
    receiveChoice() {
        this.nextSceneCtor = AssignmentInfoScene;
    }
}

export class AssignmentInfoScene extends BaseScene {
    render() {
        return [this.text(`Today ${this.state.character.name} is supposed to meet councillor Warlove to get an assignment.`),
            this.text(`But first ${this.heShe} needs to get dressed. ${this.state.character.name} opens ${this.hisHer} wardrobe closet.`)];
    }
    receiveChoice() {
        this.nextSceneCtor = commonScenes.ChooseClothesBottomScene;
    }
}