export enum GenderType {
    male = 'm',
    female = 'f',
}

export function heShe(genderType: GenderType) {
    switch (genderType) {
        case GenderType.male: return 'he';
        case GenderType.female: return 'she';
    }
}

export function hisHer(genderType: GenderType) {
    switch (genderType) {
        case GenderType.male: return 'his';
        case GenderType.female: return 'her';
    }
}

export function himselfHerself(genderType: GenderType) {
    switch (genderType) {
        case GenderType.male: return 'himself';
        case GenderType.female: return 'herself';
    }
}

export function himHer(genderType: GenderType) {
    switch (genderType) {
        case GenderType.male: return 'him';
        case GenderType.female: return 'her';
    }
}
