export function getEnumMemberName(enumType: any, key: any): string {
    for (const fieldName in enumType) {
        if (enumType[fieldName] == key)
            return fieldName;
    }
    return null;
}

/** remember that "" == 0 is true */
export function isValidEnumMember(enumType: any, enumValue: any): boolean {
    for (const fieldName in enumType) {
        if (enumType[fieldName] === enumValue)
            return true;
    }
    return false;
}