import { Component, h } from "preact";
import { StorySaveFiles } from "../common/StorySaveFiles";
import { StoryInfo } from "../common/StoryInfo";
import { APP_LOCAL_STORAGE_KEY_PREFIX } from "./GuiCommon";
import { StoryState } from "../common/StoryState";
import { StoryRunner, DebugCommandReceiver } from "../common/StoryRunner";
import { StoryScene } from "../common/StoryScene";
import { Colors } from "./Colors";
import { StoryElement } from "../common/StoryElement";
import { GuiStoryPanel } from "./GuiStoryPanel";
import { UserReactionStoryElement } from "./UserReactionStoryElement";

export class GuiRunnerProps {
    storyInfo: StoryInfo;
    receiveCommend: DebugCommandReceiver;
}

export class GuiRunnerState {
    elements: StoryElement[][] = [];
    inputText: string = '';
}

export class GuiRunner extends Component<GuiRunnerProps, GuiRunnerState> {
    private runner: StoryRunner;
    private scene: StoryScene;

    constructor(props: GuiRunnerProps) {
        super(props);
        this.runner = new StoryRunner(props.storyInfo);
        this.runner.receiveCommand = props.receiveCommend;
        this.state = new GuiRunnerState();
    }
    private static inputStyle = {
        color: Colors.solarizedBase3,
        backgroundColor: Colors.solarizedBase02,
        width: '100%',
        border: '1px solid ' + Colors.solarizedBase3,
        marginTop: '8px',
        paddingLeft: '4px',
    };
    private panelCountLimit = 16;
    render() {
        return <div>
            <div>
                {this.state.elements.map(elementSet => <GuiStoryPanel elements={elementSet}/>)}
            </div>
            <input type="text" autoFocus
                value={this.state.inputText}
                style={GuiRunner.inputStyle}
                onChange={event => this.receiveInputChange(event)}
                onKeyUp={event => this.receiveInputKeyPress(event)}
                placeholder='input here'
            />
        </div>;
    }
    componentWillMount() {
        this.start();
    }
    private loadState(fileName: string) {
        const text = localStorage.getItem(APP_LOCAL_STORAGE_KEY_PREFIX + fileName);
        if (text != null) {
            const content = JSON.parse(text);
            const state = new this.runner.storyInfo.stateConstructor;
            state.load(content);
            return state;
        } else
            return null;
    }
    private saveState(fileName: string, state: StoryState) {
        const content = JSON.stringify(state, null, 4);
        localStorage.setItem(APP_LOCAL_STORAGE_KEY_PREFIX + fileName, content);
    }
    private async start() {
        let state = this.loadState(StorySaveFiles.auto);
        if (state == null) {
            state = new this.runner.storyInfo.stateConstructor;
            console.log('Starting a new game.');
        }
        const ctor = this.runner.getRequiredSceneCtor(state.sceneClassName);
        const scene = this.runner.createSceneWithState(ctor, state);
        this.startScene(scene)
    }
    private startScene(scene: StoryScene) {
        this.saveState(StorySaveFiles.auto, scene.state);
        this.scene = scene;
        const elementSet = scene.render();
        this.addElementSet(elementSet);
        this.setState({inputText: ''});
        setTimeout(() => window.scrollTo(0,document.body.scrollHeight));
    }
    private addElementSet(elementSet: StoryElement[]) {
        const elements = this.state.elements;
        elements.push(elementSet);
        while (elements.length > this.panelCountLimit)
            elements.shift();
        this.setState({ elements });
    }

    private receiveInputChange(event) {
        this.setState({inputText: event.target.value});
    }
    private receiveInputKeyPress(event): void {
        if (event.keyCode == 13)
            this.receiveEnterEvent();
    }
    private receiveEnterEvent() {
        let resultScene: StoryScene = this.scene;
        const scene = this.scene;
        const inputText = this.state.inputText;
        const userReactionElement = new UserReactionStoryElement;
        switch (inputText) {
            case 'debug show scenes':
                userReactionElement.subText = 'Count of scenes: ' + Object.keys(this.runner.storyInfo.scenes).length;
                break;
            case 'help':
                userReactionElement.subText = 'Commands: save, load, status, reset. Empty text: continue.';
                break;
            case 'save':
                this.saveState(StorySaveFiles.quick, scene.state)
                userReactionElement.subText = 'Saved successfully.';
                break;
            case 'load':
                const state = this.loadState(StorySaveFiles.quick);
                if (state != null) {
                    const ctor = this.runner.getRequiredSceneCtor(state.sceneClassName);
                    resultScene = this.runner.createSceneWithState(ctor, state);
                }
                break;
            case 'status':
                resultScene = this.runner.createStatusScene(scene);
                break;
            case 'reset':
                resultScene = this.runner.createStartingScene();
                break;
            default:
                const isDebugCommand = this.runner.receiveCommand(inputText, scene);
                if (!isDebugCommand) {
                    this.scene.receiveChoice(inputText);
                    if (this.scene.nextSceneCtor != null) {
                        resultScene = this.runner.createSceneWithState(scene.nextSceneCtor, scene.state);
                    }
                }
                break;
        }
        if (inputText.trim().length > 0) {
            userReactionElement.text = this.state.inputText;
            this.addElementSet([userReactionElement]);
        }
        this.startScene(resultScene);
    }
}