import * as StartScene from './StartScene';
import * as IntroScenes from './IntroScenes';
import * as StatusScene from './StatusScene';
import * as DressupScenes from './DressupScenes';
import * as BreakfastScenes from './BreakfastScenes';
import * as ArtifactEntranceScenes from './ArtifactEntranceScenes';
import * as ArtifactSpiderScenes from './ArtifactSpiderScenes';
import * as GameOverScene from './GameOverScene';
import * as ArtifactRetrievalScenes from './ArtifactRetrievalScenes';
import { globalScenes } from './scenes';

const sceneGroups = [StartScene, IntroScenes, StatusScene, DressupScenes, BreakfastScenes, 
    ArtifactEntranceScenes, ArtifactSpiderScenes, GameOverScene, ArtifactRetrievalScenes];

for (const group of sceneGroups) {
    for (const sceneName in group) {
        const sceneClass = group[sceneName];
        globalScenes[sceneName] = sceneClass;
    }
}
