import { StoryState } from "../story-engine/src/common/StoryState";
import { LittleCharacter } from "./LittleCharacter";
import { ClothingType } from "./clothes";

export class LittleState extends StoryState {
    character: LittleCharacter;
    wakeUpSceneChoice: number;
    focusedClothingType: ClothingType;
    breakfast: {
        initialChoice: string;
        portionSizeChoice: string;
        muffinCount: number;
        muffinSubCounter: number;
    };
    renown: number;
    caveEntranceFailureCount: number;
    caveSpider: {
        health: number;
        rolled: boolean;
        introShown: boolean;
    };
    artifactGrabReaction: number;
    artifactIsPicked: boolean;
    artifactDrinkStopAttempted: boolean;

    load(state: LittleState) {
        super.load(state);
        if (state.character != null) {
            this.character = new LittleCharacter;
            Object.assign(this.character, state.character);
        }
    }
}