export enum BodyType {
    thin = 0,
    average = 1,
    chubby = 2,
    fat = 3,
}

export function bodyTypeToString(bodyType: BodyType) {
    switch (bodyType) {
        case BodyType.thin:
            return 'thin';
        case BodyType.average:
            return 'neither fat nor thin';
        case BodyType.chubby:
            return 'chubby';
        case BodyType.fat:
            return 'fat';                                
    }
}