import { BaseScene } from "../BaseScene";
import { commonScenes } from "./CommonScenes";
import { StoryChoice } from "../../story-engine/src/common/StoryChoice";
import { StoryElement } from "../../story-engine/src/common/StoryElement";
import { Chars } from "../chars";
import { upperFirst } from "../../story-engine/src/common/string";
import { fullnessToText, FullnessLevel } from "../food";
import { yesNoChoices } from "../YesNoChoices";
import { Confirmation } from "../Confirmation";
import { ClothingFitType } from "../clothingSizes";
import { isAre } from "../storyText";
import { clothingBottomIsPlural, clothingBottomToText } from "../clothes";
import { GenderType } from "../GenderType";
import { BodyType } from "../BodyType";

export class BreakfastStartScene extends BaseScene {
    render() {
        return [this.text('Time for breakfast'),
            new StoryChoice('rice', 'r'),
            new StoryChoice('potatoes', 'p'),
            new StoryChoice('noodles', 'n'),
            new StoryChoice('skip main course and eat some muffins', 'm'),
            new StoryChoice('skip breakfast entirely and go directly to the meeting', 'skip'),
        ];
    }
    receiveChoice(choice: string) {
        if (['r', 'p', 'n'].includes(choice))
            this.nextSceneCtor = BreakfastFirstCourseSizeScene;
        if (choice == 'm')
            this.nextSceneCtor = BreakfastDirectMuffinScene;
        if (choice == 'skip')
            this.nextSceneCtor = BreakfastSkippedScene;
        if (this.nextSceneCtor != null) {
            this.state.breakfast.initialChoice = choice;
            this.state.breakfast.muffinCount = 4;
            this.state.breakfast.muffinSubCounter = 0;
        }
    }
}
commonScenes.BreakfastStartScene = BreakfastStartScene;

export class BreakfastFirstCourseSizeScene extends BaseScene {
    render() {
        return [
            this.text(this.state.character.name + ': it wouldn\'t do to go to the meeting without having a proper meal first, right?'),
            new StoryChoice('make a small portion', 's'),
            new StoryChoice('make a large portion', 'l'),
        ];
    }
    receiveChoice(choice: string) {
        if (['s', 'l'].includes(choice)) {
            this.state.breakfast.portionSizeChoice = choice;
            this.nextSceneCtor = BreakfastFirstCourseScene;
        }
    }
}

function foodToText(key: string) {
    switch(key) {
        case 'r': return 'rice';
        case 'p': return 'potatoes';
        case 'n': return 'noodles';
    }
}

function portionSizeToText(key: string) {
    switch (key) {
        case 's': return 'small';
        case 'l': return 'large';
    }
}

function foodIsPlural(key: string) {
    switch(key) {
        case 'r': return false;
        case 'p': return true;
        case 'n': return true;
    }
}

export class BreakfastFirstCourseScene extends BaseScene {
    fullness: number;
    render() {
        this.fullness = this.state.character.fullness;
        const foodText = foodToText(this.state.breakfast.initialChoice);
        const foodPlural = foodIsPlural(this.state.breakfast.initialChoice);

        const elements: StoryElement[] = [];
        const cookingText = this.state.character.name + ' decides to make a ' +
            portionSizeToText(this.state.breakfast.portionSizeChoice) + ' portion of ' +
            foodText;
        elements.push(this.text(cookingText));

        let eatingText = 'When the meal is ready, ' + this.heShe + ' proceeds to eat the ' + foodToText(this.state.breakfast.initialChoice) + '. ';
        if (this.state.breakfast.portionSizeChoice == 's') {
            eatingText += 'Soon';
            this.fullness += 1;
        } else {
            eatingText += 'After a while';
            this.fullness += 2;
        }
        eatingText += ` all ${foodText} disappear${foodPlural ? '' : 's'} into ${this.hisHer} mouth.
            ${upperFirst(this.heShe)} feels ${fullnessToText(this.fullness)}.`;
        elements.push(this.text(eatingText));

        return elements;
    }
    receiveChoice() {
        this.nextSceneCtor = BreakfastPreConsiderMuffinsScene;
        this.state.character.fullness = this.fullness;
    }
}

export class BreakfastDirectMuffinScene extends BaseScene {
    render() {
        const text = this.state.character.name + ': Rice, potatoes... Boring stuff. I want some muffins.'
        return [this.text(text)];
    }
    receiveChoice() {
        this.nextSceneCtor = BreakfastConsiderMuffinScene;
    }
}

export class BreakfastPreConsiderMuffinsScene extends BaseScene {
    render() {
        return [this.text(`In cupboard there are some muffins`),
            new StoryChoice('Open the cupboard', 'o'),
            new StoryChoice('To h*ll with them', 'n')];
    }
    receiveChoice(choice: string) {
        switch (choice) {
            case 'o':
                this.nextSceneCtor = BreakfastConsiderMuffinScene;
                break;
            case 'n':
                this.nextSceneCtor = BreakfastEatingFinishedScene;
                break;
        }
    }
}

export class BreakfastConsiderMuffinScene extends BaseScene {
    render() {
        const muffinsArePlural = Math.abs(this.state.breakfast.muffinCount) != 1;
        const muffinsWord = muffinsArePlural ? 'muffins' : 'muffin';
        let unbuttonChoice: StoryChoice = null;
        let text = `In the cupboard there ${isAre(muffinsArePlural)} ${this.state.breakfast.muffinCount} ${muffinsWord} left. `
            + 'Should ' + this.state.character.name + ' eat one? ' + upperFirst(this.heShe) + ' feels ' + fullnessToText(this.state.character.fullness) + '.';
        if (this.state.character.fullness > FullnessLevel.full && this.state.character.getBottomClothingFitType() == ClothingFitType.tight) {
            const clothingBottom = this.state.character.getBottomClothingShortText();
            const isAreText = isAre(clothingBottomIsPlural(this.state.character.clothingBottom));
            text += ` ${upperFirst(this.hisHer)} ${clothingBottom} ${isAreText} `
                + `uncomfortably tight around ${this.hisHer} stomach.`;
            unbuttonChoice = new StoryChoice(`Undo the ${clothingBottomToText(this.state.character.clothingBottom)}`, 'u');
        }
        return [this.text(text),
            ...yesNoChoices,
            unbuttonChoice];
    }
    receiveChoice(choice: string) {
        switch(choice) {
            case Confirmation.yes:
                this.nextSceneCtor = BreakfastEatMuffinScene;
                break;
            case Confirmation.no:
                this.nextSceneCtor = BreakfastEatingFinishedScene;
                break;
            case 'u':
                this.nextSceneCtor = BreakfastTryUndoScene;
                break;
        }
    }
}

export class BreakfastTryUndoScene extends BaseScene {
    render() {
        const isPlural = clothingBottomIsPlural(this.state.character.clothingBottom);
        const itThem = isPlural ? 'them' : 'it';
        return [this.text(`${this.state.character.name}: It is not a good idea to go to the meeting with my ${clothingBottomToText(this.state.character.clothingBottom)} undone, ` +
            `so I cannot undo ${itThem} now only to find out later that I cannot button ${itThem} again later.`)];
    }
    receiveChoice() {
        this.nextSceneCtor = BreakfastConsiderMuffinScene;
    }
}

export class BreakfastEatMuffinScene extends BaseScene {
    render() {
        let text = `${this.state.character.name} decides to eat a muffin. `;
        if (this.state.breakfast.muffinCount > 0) {
            if (this.state.character.fullness >= FullnessLevel.stuffed )
                text += `${upperFirst(this.heShe)} eats the muffin slowly and laboriously.`;
            else if (this.state.character.fullness >= FullnessLevel.full)
                text += `${upperFirst(this.heShe)} eats the muffin slowly.`;
            else
                text += `${upperFirst(this.heShe)} eats the muffin quickly. Nom!`;
        } else {
            text += `But there are no muffins left in the cupboard. ${upperFirst(this.heShe)} `
                + `makes a dissatisfied sound.`;
        }
        return [this.text(text)];
    }
    receiveChoice() {
        const state = this.state;
        if (state.breakfast.muffinCount > 0) {
            this.nextSceneCtor = BreakfastConsiderMuffinScene;
            state.breakfast.muffinSubCounter += 1;
            if (state.breakfast.muffinSubCounter >= 2) {
                state.breakfast.muffinSubCounter = 0;
                state.character.fullness += 1;
            }
            state.breakfast.muffinCount -= 1;
        } else {
            this.nextSceneCtor = BreakfastConsiderMuffinScene;
        }
    }
}

export class BreakfastSkippedScene extends BaseScene {
    render() {
        return [this.text(`${this.state.character.name}: For some reason I don't feel like eating right now.`)];
    }
    receiveChoice() {
        this.nextSceneCtor = BreakfastEatingFinishedScene;
    }
}

export class BreakfastEatingFinishedScene extends BaseScene {
    sick: boolean;
    render() {
        const goesDownstairs = `goes downstairs to get ${this.hisHer} horse ready for the ride`;
        const characterGoesDownstairs = `${this.state.character.name} ${goesDownstairs}`;
        let texts: string[] = [];
        if (this.state.character.fullness <= FullnessLevel.empty) {
            texts.push(`For better or worse, ${this.state.character.name} decides to skip breakfast. ${upperFirst(this.heShe)} ${goesDownstairs}.`);
        } else if (this.state.character.fullness <= FullnessLevel.sated) {
            texts.push(`After eating a light breakfast, ${characterGoesDownstairs}.`);
        } else if (this.state.character.fullness <= FullnessLevel.full) {
            texts.push(`After having some breakfast, ${characterGoesDownstairs}.`);
        } else if (this.state.character.fullness <= FullnessLevel.stuffed) {
            texts.push(`After eating a generous amount of food, ${characterGoesDownstairs}.`);
            texts.push(...this.getStuffedFlavorText());
        } else {
            texts.push(`After stuffing ${this.himselfHerself} with as much food as possible, ${this.state.character.name} feels sick.`);
            this.sick = true;
        }
        return texts.map(t => this.text(t));
    }
    private getStuffedFlavorText(): string[] {
        const topFitType = this.state.character.getTopClothingFitType();
        const texts: string[] = [];
        if (topFitType <= ClothingFitType.tight && this.state.character.bodyType <= BodyType.chubby) {
            texts.push(`${this.state.character.name}'s ${this.state.character.getTopClothingShortText()}
                is stretched tight around ${this.hisHer} stuffed belly.`);
            let speech = `${this.state.character.name}: I hope I deflate soon`;
            if (this.state.character.gender == GenderType.female)
                speech += ` so that I don't look pregnant.`
            else
                speech += `...`;
            texts.push(speech);
        }
        return texts;
    }
    receiveChoice(choice: string) {
        if (this.sick) {
            this.nextSceneCtor = BreakfastSicknessScene;
        } else {
            this.nextSceneCtor = commonScenes.TravelToMeetingScene;
        }
    }
}

export class BreakfastSicknessScene extends BaseScene {
    render() {
        return [this.text(`${this.state.character.name}: Oof. Maybe eating all those muffins wasn't such a good idea.`),
            new StoryChoice('Try to keep it down', '1'),
            new StoryChoice('Give up', '2')];
    }
    receiveChoice(choice: string) {
        switch (choice) {
            case '1':
                this.nextSceneCtor = BreakfastKeepDownScene;
                break;
            case '2':
                this.nextSceneCtor = BreakfastThrowUpScene;
                break;
        }
    }
}

const enoughFoolingAroundText = `That's enough fooling around. I already spent too much time on eating. Time to go downstairs and get my horse ready for the ride.`;

export class BreakfastKeepDownScene extends BaseScene {
    successful: boolean;
    render() {
        let texts = [`${this.state.character.name} presses hand to ${this.hisHer} mouth and tries ${this.hisHer} best to keep the food down.`];
        this.successful = this.state.character.getBottomClothingFitType() != ClothingFitType.tight;
        if (this.successful) {
            texts.push(`${Chars.checkMark} success! ${this.state.character.name} managed to keep down the food. ${upperFirst(this.heShe)} pats ${this.hisHer} stomach lightly.`);
            texts.push(`${this.state.character.name}: Ahem... ${enoughFoolingAroundText}`)
        } else {
            const clothingBottomText = clothingBottomToText(this.state.character.clothingBottom);
            texts.push(`${Chars.failMark} but the constricting clothing doesn't help. ` +
                `${upperFirst(this.hisHer)} stomach is too stressed because ${this.hisHer} ${clothingBottomText} is compressing it.`);
        }
        return texts.map(t => this.text(t));
    }
    receiveChoice() {
        if (this.successful)
            this.nextSceneCtor = commonScenes.TravelToMeetingScene;
        else
            this.nextSceneCtor = BreakfastThrowUpScene;
    }
}

export class BreakfastThrowUpScene extends BaseScene {
    fullness: number;
    render() {
        this.fullness = this.state.character.fullness;
        this.fullness -= 2;
        const texts = [
            `${this.state.character.name} throws up into a clay pot.`,
            `${this.state.character.name}: Oops... What a great start of the day. Why did I even have to eat so much? Must be the all stress getting the best of me.`,
            `${upperFirst(this.heShe)} looks over ${this.hisHer} ${this.state.character.getTopClothingShortText()} to make sure it was not stained.`,
            `${this.state.character.name} no longer feels ${fullnessToText(this.state.character.fullness)}. ` +
                `${upperFirst(this.heShe)} now feels ${fullnessToText(this.fullness)}.`,
            `${Chars.checkMark} score -1`,
            `${this.state.character.name}: ${enoughFoolingAroundText}`
        ];
        return texts.map(t => this.text(t));
    }
    receiveChoice() {
        this.nextSceneCtor = commonScenes.TravelToMeetingScene;
        this.state.renown -= 1;
        this.state.character.fullness = this.fullness;
    }
}