import { BaseScene } from "../BaseScene";
import { StoryElement } from "../../story-engine/src/common/StoryElement";
import { StoryChoice } from "../../story-engine/src/common/StoryChoice";
import { commonScenes } from "./CommonScenes";
import { Chars } from "../chars";
import { strengthToText } from "../strength";
import { upperFirst } from "../../story-engine/src/common/string";
import { magicPowerToText } from "../magic";
import { BodyType } from "../BodyType";

enum CaveSpiderChoice {
    blast = 1,
    punch,
    roll,
    playDead
}

export class CaveSpiderScene extends BaseScene {
    render() {
        const elements: StoryElement[] = [];
        if (!this.state.caveSpider.introShown)
            elements.push(
                this.text(`About 5 minutes have passed since ${this.state.character.name} entered the cave.
                    The journey have been uneventful so far.
                    However ${this.heShe} now hears a clicking sound somewhere nearby.`),
                this.text(`A giant spider jumps onto ${this.himHer} from the dark corner.
                    ${this.state.character.name} falls to the ground.`),
                this.text(`Decide what to do to get out of this sticky situation.`),
            );
        else
            elements.push(this.text(`How to fight the giant spider?`));
        elements.push(
            this.text(`${Chars.mark} energy left: ${this.state.character.energy}`),
            new StoryChoice(`blast the spider with magic`, '' + CaveSpiderChoice.blast),
            new StoryChoice(`punch the spider`, ''+ CaveSpiderChoice.punch),
            this.state.caveSpider.rolled
                ? null
                : new StoryChoice(`roll on top of the spider`, '' + CaveSpiderChoice.roll),
            new StoryChoice(`play dead`, '' +CaveSpiderChoice.playDead)
        );
        return elements.filter(e => e != null);
    }
    receiveChoice(choiceString: string) {
        const choice = parseInt(choiceString) as CaveSpiderChoice;
        switch (choice) {
            case CaveSpiderChoice.blast:
                this.nextSceneCtor = CaveSpiderBlastScene;
                break;
            case CaveSpiderChoice.punch:
                this.nextSceneCtor = CaveSpiderPunchScene;
                break;
            case CaveSpiderChoice.roll:
                if (!this.state.caveSpider.rolled)
                    this.nextSceneCtor = CaveSpiderRollScene;
                break;
            case CaveSpiderChoice.playDead:
                this.nextSceneCtor = CaveSpiderPlayDeadScene;
                break;
        }
        if (this.nextSceneCtor != null)
            this.state.caveSpider.introShown = true;
    }
}
commonScenes.CaveSpiderScene = CaveSpiderScene;

export class CaveSpiderFightBaseScene extends BaseScene {
    localEnergy: number;
    localSpiderHealth: number;
    get resumeAttackText() {
        return `For a moment it looks disoriented,
            but then it turns its multiple eyes back to ${this.state.character.name} and hisses in ${this.hisHer} face.`;
    }
    get aggressiveAttackText() {
        return `The monster spits some cobweb into ${this.characterName}'s face.
            ${upperFirst(this.heShe)} frantically tries to get it off ${this.himHer}.`;
    }
    readLocals() {
        this.localEnergy = this.state.character.energy;
        this.localSpiderHealth = this.state.caveSpider.health;
    }
    flushLocals() {
        this.state.character.energy = this.localEnergy;
        this.state.caveSpider.health = this.localSpiderHealth;
    }
    receiveChoice(choice: string) {
        if (this.localSpiderHealth <= 0)
            this.nextSceneCtor = CaveSpiderDefeatedScene;
        else if (this.localEnergy <= 0)
            this.nextSceneCtor = commonScenes.GameOverScene;
        else
            this.nextSceneCtor = CaveSpiderScene;
        if (this.nextSceneCtor != null)
            this.flushLocals();
    }
}

export class CaveSpiderBlastScene extends CaveSpiderFightBaseScene {
    render() {
        this.readLocals();
        const elements: StoryElement[] = [];
        const magicText = magicPowerToText(this.state.character.magic);
        const secondarilySucceeded = this.state.character.magic >= 2 && this.state.caveSpider.health <= 1;
        if (this.state.character.magic >= 3 || secondarilySucceeded) {
            elements.push(
                this.text(`${Chars.checkMark} magic ${magicText}`),
                this.text(`${this.characterName} blasts the giant insect's head, and it almost evaporates! The spider drops dead.`),
                this.text(`${Chars.mark} energy -1`),
            );
            this.localEnergy -= 1;
            this.localSpiderHealth -= 2;
        } else if (this.state.character.magic >= 2) {
            elements.push(
                this.text(`${Chars.mark} magic ${magicText}`),
                this.text(`${this.characterName} blasts the giant insect's head. ${this.resumeAttackText}`),
                this.text(`${Chars.mark} energy -1, spider health -1`),
            );
            this.localEnergy -= 1;
            this.localSpiderHealth -= 1;
        } else {
            elements.push(
                this.text(`${Chars.failMark} magic: ${magicText}`),
                this.text(`${this.characterName} blasts the giant insect's head as hard as ${this.heShe} can,
                    but it's not enough to hurt it.`),
            );
            this.localEnergy -= 1;
        }
        return elements;
    }
}

export class CaveSpiderPunchScene extends CaveSpiderFightBaseScene {
    render() {
        this.readLocals();
        const strengthText = strengthToText(this.state.character.strength);
        const elements: StoryElement[] = [];
        const secondarilySucceeded = this.state.character.strength >= 2 && this.state.caveSpider.health <= 1;
        if (this.state.character.strength >= 3 || secondarilySucceeded) {
            elements.push(
                this.text(`${Chars.checkMark} strength: ${strengthText}`),
                this.text(`${this.state.character.name} punched the giant insect in the head and knocked it out!
                    To finish it off for good, ${this.heShe} uses ${this.hisHer} dagger.`),
                this.text(`${Chars.mark} energy -1`),
            );
            this.localEnergy -= 1;
            this.localSpiderHealth -= 2;
        } else if (this.state.character.strength >= 2) {
            elements.push(
                this.text(`${Chars.mark} strength ${strengthText}`),
                this.text(`${this.state.character.name} punched the giant insect in the head. ${this.resumeAttackText}`),
                this.text(`${Chars.mark} energy -1, spider health -1`),
            );
            this.localEnergy -= 1;
            this.localSpiderHealth -= 1;
        } else {
            elements.push(
                this.text(`${Chars.failMark} strength ${strengthText}`),
                this.text(`${this.characterName} punched the giant insect in the head as hard as ${this.heShe} could,
                    but it was not enough to hurt it. ${this.aggressiveAttackText}`),
                this.text(`${Chars.mark} energy -1`),
            );
            this.localEnergy -= 1;
        }
        return elements;
    }
}

export class CaveSpiderRollScene extends CaveSpiderFightBaseScene {
    render() {
        this.readLocals();
        const elements: StoryElement[] = [];
        elements.push(
            this.text(`${this.characterName} twists ${this.hisHer} body and rolls on top of the spider!`),
        );
        if (this.state.character.bodyType >= BodyType.fat) {
            elements.push(
                this.text(`${Chars.checkMark} fat`),
                this.text(`Although the spider is huge compared to normal spiders,
                    it doesn't look that big near ${this.characterName}.
                    It starts panicking being pinned under ${this.hisHer} weight.
                    ${this.characterName} has enough time to grab ${this.hisHer} dagger and finish off the creature.`),
                this.text(`${Chars.mark} energy -1`)
            );
            this.localEnergy -= 1;
            this.localSpiderHealth -= 2;
        } else if (this.state.character.bodyType >= BodyType.chubby) {
            this.localEnergy -= 1;
            this.localSpiderHealth -= 1;
            const spiderDied = this.localSpiderHealth <= 0;
            elements.push(
                this.text(`${Chars.mark} chubby`),
                this.text(`Although the spider is huge compared to normal spiders,
                    it doesn't look that big near ${this.characterName}.
                    It gets pinned under ${this.hisHer} weight. ` +
                    (spiderDied
                        ? `${this.characterName} has enough time to grab ${this.hisHer} dagger and finish off the creature.`
                        : `But seems like the creature still has some energy to fight back left in it. ${this.resumeAttackText}`)
                    ),
                this.text(`${Chars.mark} energy -1, spider health -1`),
            );
        } else {
            elements.push(
                this.text(`${Chars.failMark} body weight`),
                this.text(`However, it doesn't feel any inconvenience. ${this.aggressiveAttackText}`),
                this.text(`${Chars.mark} energy -1`)
            );
            this.localEnergy -= 1;
        }
        return elements;
    }
    receiveChoice(choice: string) {
        super.receiveChoice(choice);
        if (this.nextSceneCtor != null)
            this.state.caveSpider.rolled = true;
    }
}

export class CaveSpiderPlayDeadScene extends CaveSpiderFightBaseScene {
    render() {
        this.readLocals();
        const elements: StoryElement[] = [];
        this.localEnergy -= 1;
        return [
            this.text(`${this.characterName} tries to play dead.
                The giant spider looks at ${this.himHer} for a short while
                and starts to cover ${this.hisHer} body with thick cobweb.
                Looks like playing dead is not a good decision in this situation.
                ${this.characterName} tears cobwebs off ${this.hisHer} body and face.
                The spider makes an angry rattling sound.`),
            this.text(`${Chars.mark} energy -1`)
        ];
    }
}

export class CaveSpiderDefeatedScene extends BaseScene {
    localRenown: number;
    render() {
        this.localRenown = this.state.renown;
        const elements: StoryElement[] = [];
        elements.push(
            this.text(`Having dealt with the spider, ${this.characterName} brushes some dust off
                ${this.hisHer} ${this.state.character.getTopClothingShortText()}
                and continues moving down the path, eyeing dark corners warily.`)
        );
        const energy = this.state.character.energy > 0 ? this.state.character.energy : 0;
        elements.push(this.text(`After the encounter ${this.heShe} has ${energy} energy left.
            You gain score equal to the remaining energy: score +${energy}`));
        this.localRenown += energy;
        return elements;
    }
    receiveChoice(choice: string) {
        this.state.renown = this.localRenown;
        this.nextSceneCtor = commonScenes.ArtifactRetrievalEntranceScene;
    }
}

