import { BaseScene } from "./story/BaseScene";
import { GenderType } from "./story/GenderType";
import { BodyType } from "./story/BodyType";
import { ClothingBottom, ClothingSize } from "./story/clothes";

export function receiveCommand(command: string, scene: BaseScene) {
    switch (command) {
        case 'debug switch gender':
            scene.state.character.gender = (scene.state.character.gender == GenderType.male)
                ? GenderType.female
                : GenderType.male;
            return true;

        case 'debug body thin':
            scene.state.character.bodyType = BodyType.thin;
            return true;
        case 'debug body average':
            scene.state.character.bodyType = BodyType.average;
            return true;
        case 'debug body chubby':
            scene.state.character.bodyType = BodyType.chubby;
            return true;
        case 'debug body fat':
            scene.state.character.bodyType = BodyType.fat;
            return true;

        case 'debug strength 1':
            scene.state.character.strength = 1;
            return true;
        case 'debug strength 2':
            scene.state.character.strength = 2;
            return true;
        case 'debug strength 3':
            scene.state.character.strength = 3;
            return true;

        case 'debug magic 1':
            scene.state.character.magic = 1;
            return true;
        case 'debug magic 2':
            scene.state.character.magic = 2;
            return true;
        case 'debug magic 3':
            scene.state.character.magic = 3;
            return true;

        case 'debug wear skirt':
            scene.state.character.clothingBottom = ClothingBottom.skirt;
            return true;
        case 'debug wear bottom S':
            scene.state.character.clothingBottomSize = ClothingSize.S;
            return true;
        case 'debug wear bottom XS':
            scene.state.character.clothingBottomSize = ClothingSize.XS;
            return true;
        case 'debug wear top M':
            scene.state.character.clothingTopSize = ClothingSize.M;
            return true;
        case 'debug wear top S':
            scene.state.character.clothingTopSize = ClothingSize.S;
            return true;
        default:
            return false;
    }
}