import { BaseScene } from "../BaseScene";
import { commonScenes } from './CommonScenes';

export class StartScene extends BaseScene {
    render() {
        return [this.text('In a faraway land...'),
            this.text('...a war is brewing >:3'),
            this.text('You are a royal wizard. Help defending your country from invaders or fail trying.'),
            this.text('But first, customize your character.'),
            this.text(`Hint: in terminal version of the game press Enter to continue.
                In web-console version focus input field and press Enter to continue.`),
        ];
    }
    receiveChoice() {
        this.nextSceneCtor = commonScenes.CharacterGenderScene;
    }
}
