import { LittleState } from "./LittleState";
import { StartScene } from "./scenes/StartScene";
import { StatusScene } from "./scenes/StatusScene";
import { LittleCharacter } from "./LittleCharacter";

export class DefaultLittleState extends LittleState {
    sceneClassName = StartScene['name'];
    statusSceneClassName = StatusScene['name'];

    character = new LittleCharacter;
    breakfast = {} as any;
    renown = 0;
    caveEntranceFailureCount = 0;
    caveSpider = {
        health: 2,
        rolled: false,
        introShown: false,
    };
}