import { StoryChoice } from "../story-engine/src/common/StoryChoice";

export enum ClothingSize {
    XS = -1,
    S = 0,
    M = 1,
    L = 2,
    XL = 3,
    XXL = 4,
}

export enum ClothingBottom {
    shorts,
    pants,
    skirt
}

export function clothingBottomToText(clothing: ClothingBottom) {
    switch (clothing) {
        case ClothingBottom.shorts: return 'shorts';
        case ClothingBottom.pants: return 'pants';
        case ClothingBottom.skirt: return 'skirt';
        default: return null;
    }
}

export function clothingBottomIsPlural(clothing: ClothingBottom) {
    switch (clothing) {
        case ClothingBottom.shorts: return true;
        case ClothingBottom.pants: return true;
        case ClothingBottom.skirt: return false;
        default: return null;
    }
}

export function createClothingBottomChoice(clothing: ClothingBottom) {
    return new StoryChoice(clothingBottomToText(clothing), '' + clothing);
}

export enum ClothingTop {
    dress,
    shirt,
    tShirt
}

export function clothingTopToText(clothing: ClothingTop) {
    switch (clothing) {
        case ClothingTop.dress: return 'dress';
        case ClothingTop.shirt: return 'shirt';
        case ClothingTop.tShirt: return 'short-sleeved shirt';
        default: return null;
    }
}

export function createClothingTopChoice(clothing: ClothingTop) {
    return new StoryChoice(clothingTopToText(clothing), '' + clothing);
}

export enum ClothingType {
    top,
    bottom
}