import { BaseScene } from "../BaseScene";
import { ClothingBottom, createClothingBottomChoice, ClothingType, clothingBottomToText, ClothingSize, createClothingTopChoice, ClothingTop, clothingTopToText, clothingBottomIsPlural } from "../clothes";
import { commonScenes } from "./CommonScenes";
import { isValidEnumMember } from "../../story-engine/src/common/enum";
import { getAvailableClothingSizeChoices, ClothingFitType } from "../clothingSizes";
import { getColorChoices, StoryColor } from "../color";
import { StoryChoice } from "../../story-engine/src/common/StoryChoice";
import { yesNoChoices } from "../YesNoChoices";
import { Confirmation } from "../Confirmation";
import { getRandomInteger } from "../../story-engine/src/common/random";
import { upperFirst } from "../../story-engine/src/common/string";

export class ChooseBottomClothingScene extends BaseScene {
    render() {
        return [
            this.text('Choose bottom clothing:'),
            createClothingBottomChoice(ClothingBottom.shorts),
            createClothingBottomChoice(ClothingBottom.pants),
            createClothingBottomChoice(ClothingBottom.skirt),
            new StoryChoice('dress (whole body)', 'd'),
        ];
    }
    receiveChoice(choice: string) {
        const clothingIndex = parseInt(choice);
        if (choice == 'd') {
            this.state.character.clothingBottom = null;
            this.state.character.clothingTop = ClothingTop.dress;
            this.nextSceneCtor = ChooseTopClothingSizeScene;
        } else if (isValidEnumMember(ClothingBottom, clothingIndex)) {
            this.state.character.clothingBottom = clothingIndex;
            this.state.focusedClothingType = ClothingType.bottom;
            this.nextSceneCtor = ChooseBottomClothingSizeScene;
        }
    }
}
commonScenes.ChooseClothesBottomScene = ChooseBottomClothingScene;

export class ChooseBottomClothingSizeScene extends BaseScene {
    render() {
        const clothing = clothingBottomToText(this.state.character.clothingBottom);
        return [
            this.text('The size of ' +  clothing + ' happens to be'),
            ...getAvailableClothingSizeChoices(this.state.character.bodyType)
        ];
    }
    receiveChoice(choice: string) {
        const size = parseInt(choice);
        if (isValidEnumMember(ClothingSize, size)) {
            this.state.character.clothingBottomSize = size;
            this.nextSceneCtor = ChooseBottomClothingColorScene;
        }
    }
}

export class ChooseBottomClothingColorScene extends BaseScene {
    render() {
        const clothing = clothingBottomToText(this.state.character.clothingBottom);
        return [
            this.text('The color of ' +  clothing + ' happens to be'),
            ...getColorChoices()
        ];
    }
    receiveChoice(choice: string) {
        const color = parseInt(choice);
        if (isValidEnumMember(StoryColor, color)) {
            this.state.character.clothingBottomColor = color;
            this.nextSceneCtor = ChooseTopClothingScene;
        }
    }
}

export class ChooseTopClothingScene extends BaseScene {
    clothes = [ClothingTop.shirt, ClothingTop.tShirt];
    render() {
        return [
            this.text('Choose top clothing:'),
            ...this.clothes.map(clothing => createClothingTopChoice(clothing))
        ];
    }
    receiveChoice(choice: string) {
        const clothing = parseInt(choice);
        if (this.clothes.includes(clothing)) {
            this.state.character.clothingTop = clothing;
            this.nextSceneCtor = ChooseTopClothingSizeScene;
        }
    }
}

export class ChooseTopClothingSizeScene extends BaseScene {
    render() {
        const clothing = clothingTopToText(this.state.character.clothingTop);
        return [
            this.text('The size of ' + clothing + ' happens to be'),
            ...getAvailableClothingSizeChoices(this.state.character.bodyType),
        ];
    }
    receiveChoice(choice: string) {
        const sizeIndex = parseInt(choice);
        if (isValidEnumMember(ClothingSize, sizeIndex)) {
            this.state.character.clothingTopSize = sizeIndex;
            this.nextSceneCtor = ChooseTopClothingColorScene;
        }
    }
}

export class ChooseTopClothingColorScene extends BaseScene {
    render() {
        const clothing = clothingTopToText(this.state.character.clothingTop);
        return [
            this.text('The color of ' + clothing + ' happens to be'),
            ...getColorChoices()
        ];
    }
    receiveChoice(choice: string) {
        const colorIndex = parseInt(choice);
        if (isValidEnumMember(StoryColor, colorIndex)) {
            this.state.character.clothingTopColor = colorIndex;
            this.nextSceneCtor = ClothingChoiceConfirmationScene;
        }
    }
}

export class ClothingChoiceConfirmationScene extends BaseScene {
    render() {
        const descriptions = this.state.character.getClothingDescriptions();
        return [
            this.text('Confirm choice of clothing:'),
            ...descriptions.map(description => this.text(description)),
            ...yesNoChoices
        ];
    }
    receiveChoice(choice: string) {
        switch (choice) {
            case Confirmation.no:
                this.nextSceneCtor = ClothingRedoScene;
                break;
            case Confirmation.yes:
                this.nextSceneCtor = ClothingPutOnScene;
                break;
        }
    }
}

export class ClothingRedoScene extends BaseScene {
    render() {
        let complaintTarget = `these clothes`;
        const thoughtFlavor = getRandomInteger(2);
        if (thoughtFlavor == 0 && this.state.character.clothingTop != null)
            complaintTarget = 'this ' + this.state.character.getTopClothingShortText();
        if (thoughtFlavor == 1 && this.state.character.clothingBottom != null)
            complaintTarget = 'this ' + this.state.character.getBottomClothingShortText();
        return [
            this.text(`${this.state.character.name}: What's with ${complaintTarget}? I want to wear something different.`)
        ]
    }
    receiveChoice() {
        this.state.character.clothingBottom = null;
        this.state.character.clothingTop = null;
        this.nextSceneCtor = ChooseBottomClothingScene;
    }
}

export class ClothingPutOnScene extends BaseScene {
    render() {
        const texts: string[] = [];
        let text = `${this.state.character.name} puts on ${this.state.character.getTopClothingShortText()}`;
        if (this.state.character.clothingBottom != null)
            text += ' and ' + this.state.character.getBottomClothingShortText();
        text += '.';
        texts.push(text);
        const bottomFitType = this.state.character.getBottomClothingFitType();
        switch (bottomFitType) {
            case ClothingFitType.loose:
                const isWord = clothingBottomIsPlural(this.state.character.clothingBottom) ? 'are' : 'is';
                texts.push('Because ' + clothingBottomToText(this.state.character.clothingBottom) +
                    ` ${isWord} loose on ${this.himHer}, ${this.heShe} wears a belt.`);
                break;
            case ClothingFitType.perfect:
                const fitsWord = clothingBottomIsPlural(this.state.character.clothingBottom) ? 'fit' : 'fits';
                texts.push('The ' + clothingBottomToText(this.state.character.clothingBottom) +
                    ` ${fitsWord} ${this.himHer} nicely.`);
                break;
            case ClothingFitType.tight:
                let text = this.state.character.name + ' struggles a little while buttoning the ' +
                    clothingBottomToText(this.state.character.clothingBottom) + '.';
                if (this.state.character.clothingBottom == ClothingBottom.skirt)
                    text = `The skirt pinches ${this.hisHer} waist
                        but its hem is still fairly wide to let the wearer ride a horse easily.`;
                texts.push(text);
                break;
        }
        const topFitType = this.state.character.getTopClothingFitType();
        if (this.state.character.clothingTop == ClothingTop.dress) {
            if (topFitType == ClothingFitType.tight) {
                let text = `${upperFirst(this.heShe)} struggles a little with ${this.hisHer} dress
                    because of how tight it is.`
                texts.push(text);
            } else {
                let text = `The dress fits ${this.himHer} nicely.`;
                texts.push(text);
            }
        }
        return [
            ...texts.map(t => this.text(t))
        ];
    }
    receiveChoice() {
        this.nextSceneCtor = commonScenes.BreakfastStartScene;
    }
}