import { StoryChoice } from "../story-engine/src/common/StoryChoice";

export enum StoryColor {
    white,
    black,
    red,
    orange,
    yellow,
    green,
    cyan,
    blue,
    purple,
}

export function getColorChoices() {
    const choices: StoryChoice[] = [];
    for (const color in StoryColor) {
        const value = StoryColor[color];
        if (typeof value == 'number') {
            choices.push(new StoryChoice(color, value));
        }
    }
    return choices;
}

export function storyColorToText(color: StoryColor) {
    return StoryColor[color];
}