import { BaseScene } from "../BaseScene";
import { StoryChoice } from "../../story-engine/src/common/StoryChoice";
import { upperFirst } from "../../story-engine/src/common/string";
import { commonScenes } from "./CommonScenes";
import { Chars } from "../chars";
import { BodyType } from "../BodyType";
import { StoryText } from "../../story-engine/src/common/StoryText";
import { lockNumberBetween } from "../../story-engine/src/math";
import { magicPowerToText } from "../magic";
import { fullnessToText, FullnessLevel } from "../food";
import { StoryElement } from "../../story-engine/src/common/StoryElement";

const documentation = `
    Jump:
        Succeeds when (bodyType + 1 < strength).
        thin (0 + 1) < (1) mediocre_strength = fail;
        chubby (2 + 1) < (2) average strength = fail;
        thin (0 + 1) < (2) average_strength = success;
        average (1 + 1) < (3) strong_strength = success;
    Fly:
        Energy required to fly equals body type enum number.
`;

class ArtifactRetrievalBaseScene extends BaseScene {
    protected get drownedText() {
        return `We will never know exactly why but
            ${this.characterName}'s head never rises above the water level again.
            It seems that the green liquid had some dangerous effect after all.`;
    }
    protected get flightCost(): number {
        return lockNumberBetween(1, this.state.character.bodyType, 3);
    }
    protected get drunkText() {
        return `Having consumed too much magical liquid, ${this.characterName} feels dizzy.
            ${upperFirst(this.hisHer)} hand slips and ${this.heShe} falls into the pool head-first.`;
    }
}

export class ArtifactRetrievalEntranceScene extends BaseScene {
    render() {
        const elements = [
            this.text(`The tunnel leads ${this.characterName} into a large circular chamber.
                At the center of the chamber there is a basin filled with green non-transparent liquid.
                The water is glowing softly.
                At the center of the pool a plinth made of stone rises slightly out of the water.
                On top of the plinth a small prism is placed.
                ${this.characterName} comes closer and stops at the edge of the pool.
                The prism seems to be made of green glass.`),
            this.text(`${this.characterName}: Finally found it. Now how do I get across?`),
            this.text(`The plinth is slightly more than 1 meter away from the edge of the pool`),
        ];
        if (this.state.character.energy < 3)
            this.text(`${this.characterName} stops to take a breath (energy restored -> 3).`);
        return elements;
    }
    receiveChoice() {
        this.nextSceneCtor = ArtifactRetrievalGapChoiceScene;
        this.state.character.energy = 3;
    }
}
commonScenes.ArtifactRetrievalEntranceScene = ArtifactRetrievalEntranceScene;

export class ArtifactRetrievalGapChoiceScene extends BaseScene {
    render() {
        const textStart = this.state.artifactIsPicked
            ? `How should ${this.characterName} cross the gap on ${this.hisHer} way back?
                ${upperFirst(this.heShe)} has to cross the pool of water again to get from the stone island to the mainland.`
            : `How should ${this.characterName} cross the gap?`
        return [
            this.text(`${textStart} The glowing water might be not safe.
                ${upperFirst(this.heShe)} cannot tell how deep the pool is, because the water is not transparent.`),
            this.text(`[x] ${this.characterName} can try to jump across the water but it is probably going to be difficult`),
            this.text(`[x] ${this.characterName} can fly above the water using a magic spell`),
            this.text(`[x] ${this.characterName} can swim carefully while trying to keep ${this.hisHer} head above the water all the time
                and not letting the water get accidentally get into ${this.hisHer} mouth`),
            this.text(`[x] ${this.characterName} can try to drink the water, though it is highly unlikely that ${this.heShe} can drain the pool this way`),
            new StoryChoice(`jump`, '1'),
            new StoryChoice(`fly`, '2'),
            new StoryChoice(`swim`, '3'),
            new StoryChoice(`drink`, '4'),
        ];
    }
    receiveChoice(choice: string) {
        switch (choice) {
            case '1':
                this.nextSceneCtor = ArtifactRetrievalJumpScene;
                break;
            case '2':
                this.nextSceneCtor = ArtifactRetrievalConsiderFlyScene;
                break;
            case '3':
                this.nextSceneCtor = ArtifactRetrievalSwimScene;
                break;
            case '4':
                this.nextSceneCtor = ArtifactRetrievalDrinkScene;
                break;
        }
    }
}

export class ArtifactRetrievalJumpScene extends ArtifactRetrievalBaseScene {
    private success: boolean;
    private localEnergy: number;
    render() {
        return this.isTooFull ? this.renderTooFullPath() : this.renderNormalPath();
    }
    renderTooFullPath() {
        return [
            this.text(`${this.characterName} considers jumping, however ${this.heShe} can't do it
                because ${this.heShe} feels ${fullnessToText(this.state.character.fullness)}.`),
            this.text(`${this.characterName}: Oof! Why did I have to drink this stupid stuff? No way I'm jumping now...`)
        ]
    }
    renderNormalPath() {
        this.localEnergy = this.state.character.energy;
        this.success = this.state.character.bodyType + 1 < this.state.character.strength;
        const partialSuccess = this.state.character.bodyType < this.state.character.strength;
        const successText = `(${Chars.checkMark} str & wgt) Success. ${this.characterName} lands on the plinth gracefully`;
        const partialSuccessText = `(${Chars.mark} str & wgt, partial)
            ${this.characterName} lands very close to the plinth but not close enough.
            ${upperFirst(this.heShe)} hits the plinth at an awkward angle and slips into the green liquid.
            ${this.drownedText}`;
        const failureText = `(${Chars.failMark} str & wgt)
            ${this.characterName} could not jump far enough to cross the gap (not even close) and
            fell into the green water in between the plinth and the edge from which ${this.heShe} jumped from.
            ${this.drownedText}`;
        this.localEnergy -= 1;
        const elements = [
            this.text(`${this.characterName} decides to jump across the water onto the plinth.`)
        ];
        elements.push(this.text(`${Chars.mark} energy -1`));
        if (this.success)
            elements.push(this.text(`${Chars.mark} score +1`));
        const actionDescriptionText =
            this.success
                ? this.text(successText)
                : partialSuccess
                    ? this.text(partialSuccessText)
                    : this.text(failureText);
        elements.push(actionDescriptionText);
        return elements;
    }
    get successText() {
        return `(${Chars.checkMark} str & wgt) Success. ${this.characterName} lands on the plinth gracefully`;
    }
    receiveChoice() {
        if (this.isTooFull) {
            this.nextSceneCtor = ArtifactRetrievalGapChoiceScene;
        } else {
            if (this.success) {
                if (this.state.artifactIsPicked)
                    this.nextSceneCtor = ArtifactRetrievalExitScene;
                else
                    this.nextSceneCtor = ArtifactRetrievalGrabScene;
            } else
                this.nextSceneCtor = commonScenes.GameOverScene;
            if (this.nextSceneCtor != null) {
                this.state.character.energy = this.localEnergy;
                if (this.success)
                    this.state.renown += 1;
            }
        }
    }
    get isTooFull() {
        return this.state.character.fullness >= FullnessLevel.veryBloated;
    }
}

export class ArtifactRetrievalConsiderFlyScene extends ArtifactRetrievalBaseScene {
    private canFly: boolean;
    render() {
        this.canFly = this.state.character.magic > 1;
        const elements = [
            this.text(`${this.characterName} considers using spell of flight. The amount of energy
                required for the spell depends on ${this.hisHer} body weight.`)
        ];
        if (this.canFly) {
            switch (this.flightCost) {
                case 1:
                    elements.push(this.text('The spell requires only a little energy: 1.'));
                    break;
                case 2:
                    elements.push(this.text(`The spell requires an average amount of energy: 2`));
                    break;
                case 3:
                    elements.push(this.text(`The spell requires a large amount of energy: 3`));
                    break;
            }
            elements.push(
                this.text(`${this.characterName} currently has ${this.state.character.energy} energy.`),
                new StoryChoice('fly', '1'),
                new StoryChoice('reconsider', '2')
            );
        } else {
            elements.push(this.text(`${this.characterName}'s magic mastery level is
                ${magicPowerToText(this.state.character.magic)}, which is not enough to fly.`),
                new StoryChoice('reconsider', '2')
            )
        }
        return elements;
    }
    receiveChoice(choiceId: string) {
        switch(choiceId) {
            case '1':
                if (this.canFly)
                    this.nextSceneCtor = ArtifactRetrievalFlyScene;
                break;
            case '2':
                this.nextSceneCtor = ArtifactRetrievalGapChoiceScene;
                break;
        }
    }
}

enum FlyOutcome {
    none = 0,
    success = 1,
    drowned = 2,
}

export class ArtifactRetrievalFlyScene extends ArtifactRetrievalBaseScene {
    private remainingEnergy: number;
    private outcome: FlyOutcome;
    render() {
        const elements: StoryText[] = [];
        this.remainingEnergy = this.state.character.energy - this.flightCost;
        const reportOutcome = (outcome: FlyOutcome, text: string) => {
            elements.push(this.text(text));
            this.outcome = outcome;
        };
        if (this.state.character.energy <= 0)
            reportOutcome(FlyOutcome.none, `${this.characterName} attempts to take off but ${this.heShe} is too tired (no energy).`);
        else if (this.remainingEnergy >= 0)
            reportOutcome(FlyOutcome.success, `${this.characterName} flew over the green water easily and landed onto the stone floor.`);
        else if (this.remainingEnergy >= -1)
            reportOutcome(FlyOutcome.success, `${this.characterName} flies over the green water but, having made half of the way,
                ${this.heShe} feels drained and has to give it ${this.hisHer} all to avoid plummeting down.
                ${this.characterName} lands onto the stone floor exhausted and stops to catch ${this.hisHer} breath.`);
        else if (this.remainingEnergy < -1)
            reportOutcome(FlyOutcome.drowned, `${this.characterName} flies over the green water but, having made half of the way,
                ${this.heShe} feels drained and tries to give it ${this.hisHer} all to avoid plummeting down.
                Unfortunately, it's not enough and ${this.heShe} falls into the glowing green water. ${this.drownedText}`);
        if (this.outcome != FlyOutcome.none)
            elements.push(this.text(`${Chars.mark} energy -${this.flightCost}`));
        if (this.outcome == FlyOutcome.success)
            elements.push(this.text(`${Chars.mark} score +1`))
        return elements;
    }
    receiveChoice() {
        switch(this.outcome) {
            case FlyOutcome.none:
                this.nextSceneCtor = ArtifactRetrievalGapChoiceScene;
                break;
            case FlyOutcome.success:
                if (this.state.artifactIsPicked)
                    this.nextSceneCtor = ArtifactRetrievalExitScene;
                else
                    this.nextSceneCtor = ArtifactRetrievalGrabScene;
                this.state.character.energy = this.remainingEnergy;
                this.state.renown += 1;
                break;
            case FlyOutcome.drowned:
                this.nextSceneCtor = commonScenes.GameOverScene;
                this.state.character.energy = this.remainingEnergy;
                break;
            default:
                console.warn('Unexpected outcome: ' + this.outcome);
        }
    }
}

export class ArtifactRetrievalGrabScene extends ArtifactRetrievalBaseScene {
    render() {
        const elements = [
            this.text(`Having reached ${this.hisHer} destination,
                ${this.characterName} grabs the prism. It shimmers slightly in the dim light.`),
            new StoryChoice('cheerful', '1'),
            new StoryChoice('annoyed', '2')
        ];
        return elements;
    }
    receiveChoice(choiceId: string) {
        const choice = parseInt(choiceId);
        const choiceIsValid = !isNaN(choice);
        if (choiceIsValid) {
            this.state.artifactGrabReaction = choice;
            this.nextSceneCtor = ArtifactRetrievalGrabReactScene;
        }
        if (this.nextSceneCtor != null)
            this.state.artifactIsPicked = true;
    }
}

export class ArtifactRetrievalGrabReactScene extends ArtifactRetrievalBaseScene {
    render() {
        let thoughts: string;
        switch (this.state.artifactGrabReaction) {
            case 1:
                thoughts = `Ooh, so this must be the Prims of Life? Fascinating.
                    And I did a good job finding it.
                    I mean, there's nobody around to praise me, so I might as well do that myself.
                    Now let's head back.`;
                break;
            case 2:
                thoughts = `So this must be the so-called Prism of Life? Whatever.
                    I can't believe what I had to go through to get it.
                    A giant spider? Stupid glowing water?
                    At least now I can finally head back.`;
                break;
        }
        const elements = [this.text(this.characterName + ': ' + thoughts)];
        return elements;
    }
    receiveChoice() {
        this.nextSceneCtor = ArtifactRetrievalGapChoiceScene;
    }
}

export class ArtifactRetrievalSwimScene extends ArtifactRetrievalBaseScene {
    render() {
        const thick = this.state.character.bodyType == BodyType.fat ? 'thick ' : '';
        return [
            this.text(`${this.characterName} decides to swim across.
                Carefully ${this.heShe} sits on the edge and lowers ${this.hisHer} first ${thick}leg into the green water, then the other.
                Afterwards ${this.heShe} proceeds to lower ${this.hisHer} whole body up to ${this.hisHer} neck and swim.`),
            this.text(`${this.characterName} managed to reach the opposite edge safely without swallowing any liquid.
                However ${this.hisHer} ${this.state.character.getTopClothingShortText()} is now soaked wet${this.clothingAsWell}`),
            this.text(`${Chars.mark} no score gain`)
        ]
    }
    private get clothingAsWell() {
        const bottomClothingText = this.state.character.getBottomClothingShortText();
        return bottomClothingText != null ? ` as well as ${this.hisHer} ${bottomClothingText}` : '';
    }
    receiveChoice() {
        if (this.state.artifactIsPicked)
            this.nextSceneCtor = ArtifactRetrievalExitScene;
        else
            this.nextSceneCtor = ArtifactRetrievalGrabScene;
    }
}

export class ArtifactRetrievalDrinkScene extends ArtifactRetrievalBaseScene {
    localFullness: number;
    tooFull: boolean;
    render() {
        this.localFullness = this.state.character.fullness + 1;
        const elements = [
            this.text(`For better or worse, ${this.characterName} decides to drink the liquid from the pool.
                ${upperFirst(this.heShe)} gets on ${this.hisHer} knees and lowers
                ${this.hisHer} head awkwardly to the ground level.
                Then ${this.heShe} starts to drink. The liquid tastes funny but not bad.
                ${this.characterName} gulps down about 1 glass worth of liquid in volume.`),
            this.text(Chars.mark + ' ' + this.characterName + ' now feels ' + fullnessToText(this.localFullness)),
        ];
        if (this.localFullness <= FullnessLevel.veryBloated) {
            elements.push(new StoryChoice('continue drinking', '1'));
            elements.push(new StoryChoice('think better of it', '2'));
        } else {
            elements.push(this.text(this.drunkText), this.text(this.drownedText));
            this.tooFull = true;
        }
        return elements;
    }
    receiveChoice(choiceId: string) {
        if (this.tooFull)
            this.nextSceneCtor = commonScenes.GameOverScene;
        else if (choiceId == '1') // continue
            this.nextSceneCtor = ArtifactRetrievalContinuedDrinkScene;
        else if (choiceId == '2') { // cancel
            if (this.localFullness < 2)
                this.nextSceneCtor = ArtifactRetrievalDrinkCancelScene;
            else
                this.nextSceneCtor = ArtifactRetrievalDrinkCancelAttemptScene;
        }
        if (this.nextSceneCtor != null)
            this.state.character.fullness = this.localFullness;
    }
}

export class ArtifactRetrievalDrinkCancelScene extends ArtifactRetrievalBaseScene {
    render() {
        const elements = [
            this.text(`${this.characterName} stands up and brushes the dust off ${this.hisHer}
                ${this.state.character.getInferredBottomClothingShortText()}.`)
        ];
        if (this.state.character.fullness >= FullnessLevel.stuffed)
            elements.push(this.text(`${upperFirst(this.heShe)} feels liquid sloshing in ${this.hisHer} stomach.`));
        return elements;
    }
    receiveChoice() {
        this.nextSceneCtor = ArtifactRetrievalGapChoiceScene;
    }
}

export class ArtifactRetrievalDrinkCancelAttemptScene extends ArtifactRetrievalBaseScene {
    localFullness: number;
    stopped: boolean;
    render() {
        const elements: StoryElement[] = [];
        this.localFullness = this.state.character.fullness;
        if (this.localFullness < FullnessLevel.veryBloated) {
            elements.push(this.text(`${this.characterName} tries to stop drinking the liquid,
                however the liquid's taste is too addictive.
                ${upperFirst(this.heShe)} continues drinking the magical liquid greedily.`));
            this.localFullness += 1;
        } else {
            elements.push(this.text(`The liquid's taste is very addictive,
                however ${this.characterName} manages to stop drinking it at last.`))
            this.stopped = true;
        }
        elements.push(this.text(Chars.mark + ' ' + this.characterName + ' now feels ' + fullnessToText(this.localFullness)));
        if (!this.stopped) {
            elements.push(new StoryChoice('continue drinking', '1'));
            elements.push(new StoryChoice('stop drinking', '2'));
        }
        return elements;
    }
    receiveChoice(choiceId: string) {
        if (this.stopped)
            this.nextSceneCtor = ArtifactRetrievalDrinkCancelScene;
        else if (choiceId == '1')
            this.nextSceneCtor = ArtifactRetrievalContinuedDrinkScene;
        else if (choiceId == '2')
            this.nextSceneCtor = ArtifactRetrievalDrinkCancelAttemptScene;
        if (this.nextSceneCtor != null)
            this.state.character.fullness = this.localFullness;
    }
}

export class ArtifactRetrievalContinuedDrinkScene extends ArtifactRetrievalBaseScene {
    localFullness: number;
    tooFull: boolean;
    render() {
        this.localFullness = this.state.character.fullness + 1;
        const elements = [this.text(`${this.characterName} drinks liquid from the pool. Its taste is somewhat addictive`),
            this.text(Chars.mark + ' ' + this.characterName + ' now feels ' + fullnessToText(this.localFullness)),
        ];
        if (this.localFullness <= FullnessLevel.veryBloated) {
            elements.push(new StoryChoice('continue drinking', '1'));
            elements.push(new StoryChoice('stop drinking', '2'));
        } else {
            elements.push(this.text(this.drunkText), this.text(this.drownedText));
            this.tooFull = true;
        }
        return elements;
    }
    receiveChoice(choiceId: string) {
        if (this.tooFull)
            this.nextSceneCtor = commonScenes.GameOverScene;
        else if (choiceId == '1')
            this.nextSceneCtor = ArtifactRetrievalContinuedDrinkScene;
        else if (choiceId == '2')
            this.nextSceneCtor = ArtifactRetrievalDrinkCancelAttemptScene;
        if (this.nextSceneCtor != null)
            this.state.character.fullness = this.localFullness;
    }
}

export class ArtifactRetrievalExitScene extends ArtifactRetrievalBaseScene {
    render() {
        const elements = [
            this.text(`On ${this.hisHer} way back ${this.characterName} doesn't encounter any more dangers or difficulties.
                ${upperFirst(this.heShe)} returns to the cave entrance with the Prism of Life in hand.`),
            this.text(`${Chars.mark} This is the end of the prototype game content.`),
            this.text(`${Chars.mark} Your score: ${this.state.renown}`)
        ];
        return elements;
    }
}