import { BodyType } from "./BodyType";
import { ClothingSize } from "./clothes";
import { isValidEnumMember } from "../story-engine/src/common/enum";
import { StoryChoice } from "../story-engine/src/common/StoryChoice";

function getAvailableClothingSizes(bodyType: BodyType) {
    const result: ClothingSize[] = [];
    for (let i = bodyType - 1; i <= bodyType + 1; i++) {
        if (isValidEnumMember(ClothingSize, i))
            result.push(i);
    }
    return result;
}

export function getAvailableClothingSizeChoices(bodyType: BodyType) {
    const sizes = getAvailableClothingSizes(bodyType);
    return sizes.map(size => {
        const fitType = getClothingSizeToBodyTypeRelation(size, bodyType);
        const fitTypeText = clothingFitTypeToText(fitType);
        return new StoryChoice('size ' + ClothingSize[size] + ', ' + fitTypeText, '' + size);
    });
}

export enum ClothingFitType {
    burst,
    tight,
    perfect,
    loose,
    fallThrough,
}

export function getClothingSizeToBodyTypeRelation(clothingSize: ClothingSize, bodyType: BodyType) {
    let result: ClothingFitType = null;
    if (bodyType < clothingSize - 1)
        result = ClothingFitType.fallThrough;
    else if (bodyType < clothingSize)
        result = ClothingFitType.loose;
    else if (bodyType == clothingSize as number)
        result = ClothingFitType.perfect;
    else if (clothingSize < bodyType - 1)
        result = ClothingFitType.burst;
    else if (clothingSize < bodyType)
        result = ClothingFitType.tight;
    return result;
}

export function clothingFitTypeToText(clothingFitType: ClothingFitType) {
    switch (clothingFitType) {
        case ClothingFitType.burst: return 'too tight';
        case ClothingFitType.tight: return 'tight fit';
        case ClothingFitType.perfect: return 'regular fit';
        case ClothingFitType.loose: return 'loose fit';
        case ClothingFitType.fallThrough: return 'too loose';
        default: return null;
    }
}