import { BaseScene } from "../BaseScene";
import { commonScenes } from "./CommonScenes";
import { StoryChoice } from "../../story-engine/src/common/StoryChoice";
import { upperFirst } from "../../story-engine/src/common/string";
import { Chars, getSuccessFailureChar } from "../chars";
import { strengthToText } from "../strength";
import { magicPowerToText } from "../magic";
import { BodyType } from "../BodyType";
import { StoryElement } from "../../story-engine/src/common/StoryElement";

export class TravelToMeetingScene extends BaseScene {
    render() {
        return [this.text(`${this.state.character.name} travels to the castle to meet councillor Warlove.`),
            this.text(`Warlove: Good day ${this.state.character.name}.
                We recently discovered the location of a powerful artifact: the Prism of Life.
                You have to retrieve this artifact as soon as possible, before someone else snatches the prism.
                The mission is to be carried out in secrecy: the fewer people know about the Prism, the better.
                As you know we suspect that our neighbors Sunflower Elves plan an attack on our lands.
                Therefore we need all the advantage we can get. The Prism of Life shall prove to be useful.`)];
    }
    receiveChoice() {
        this.nextSceneCtor = TravelToCaveScene;
        this.state.character.fullness = 0;
    }
}
commonScenes.TravelToMeetingScene = TravelToMeetingScene;

export class TravelToCaveScene extends BaseScene {
    render() {
        const texts = [
            `Warlove: our scouts found this old map. A temple is marked on it, but there are no known settlements nearby.`,
            `Councillor Warlove gives ${this.state.character.name} a map pointing to the location where the magical artifact is supposed to be
                and sends ${this.himHer} on ${this.hisHer} way.`,
            `After 3 days of travel ${this.state.character.name} reaches Brightwall, a small town where ${this.heShe} stops for the night in a tavern.`,
            `Zzz... Fast-forward to the morning. Now might be a good time to save the game if you haven't done that recently.
            In terminal version type "help" to review list of commands.`,
            `${this.state.character.name} starts the day with 3 energy.`,
            `After two more hours of travel ${this.state.character.name} reaches ${this.hisHer} destination.
                The entrance to the cave is hidden behind a waterfall. A thick green grove surrounds it.`
        ];
        return texts.map(t => this.text(t));
    }
    receiveChoice() {
        this.state.character.energy = 3;
        this.nextSceneCtor = PreCaveEntranceScene;
    }
}

export class PreCaveEntranceScene extends BaseScene {
    render() {
        return [
            this.text(`The way inside is blocked by a wall made of large stone blocks. It seals the cave entrance.
                The stone blocks are covered with moss. In the middle there is a door made of stone.`),
            this.text(`Now it is time to solve the first puzzle.`),
            this.text(`[x] ${this.state.character.name} can try to open the door using ${this.hisHer} physical strength.
                First ${this.heShe} needs to force it open just slightly to open a gap, so that ${this.heShe} can grasp its side with ${this.hisHer} hands.
                For this purpose ${this.heShe} can use ${this.hisHer} dagger made of tempered steel.
                Then hopefully ${this.heShe} can make the gap wide enough to get ${this.himselfHerself} inside.
                ${this.state.character.name}'s dagger is not great as a lever because it is short, so ${this.heShe} would have to pull it pretty hard.`),
            this.text(`[x] The second approach is a bit like the first but ${this.state.character.name} can use magic to enhance ${this.hisHer} strength.
                It works best if ${this.heShe} has a little of both magical and physical strength
                but the downside is that the effort might leave ${this.state.character.name} tired
                because ${this.heShe} would have to exert ${this.himselfHerself} both physically and spiritually.`),
            this.text(`[x] The third approach is to pull the door open with a magic spell.
                The spell itself is not complicated for a wizard like ${this.state.character.name}.
                But the sheer weight of the pulled object makes the task harder.`),
            this.text(`[x] Finally, about three steps away there is a narrow cleft in the cliff's wall. The path continues inside turning left and right.
                It is not possible to see where it leads, but it goes in the general direction of the cave, so hopefully it leads inside.
                The tunnel's walls are wet with moisture, probably because of the proximity to the waterfall.
                Trying to traverse this path seems to be a good choice for someone who is thin (but even if you aren't, nothing stops you from trying).`),
            new StoryChoice('use strength to open the door', '1'),
            new StoryChoice('use enhanced strength', '2'),
            new StoryChoice('use magic', '3'),
            new StoryChoice('go through the narrow tunnel', '4'),
        ];
    }
    receiveChoice(choice: string) {
        switch (choice) {
            case '1':
                this.nextSceneCtor = CaveEntranceStrengthScene;
                break;
            case '2':
                this.nextSceneCtor = CaveEntranceEnhancedStrengthScene;
                break;
            case '3':
                this.nextSceneCtor = CaveEntranceMagicScene;
                break;
            case '4':
                this.nextSceneCtor = CaveEntranceTunnelScene;
                break;
        }
    }
}

class CaveEntranceStrengthBaseScene extends BaseScene {
    get mainText() {
        return `${upperFirst(this.heShe)} sticks ${this.hisHer} dagger
            into the gap between the wall and the door
            and pulls with all ${this.hisHer} might.`;
    }
    get successText() {
        return `Slowly, inch by inch, the door opens. Success! After opening a gap
            ${this.state.character.name} widens it the rest of the way and enters the cave.`;
    }
    get failureText() {
        return `For several minutes ${this.state.character.name} pushes and pulls while making grunting noises. But the door will not budge.`;
    }
}

export class CaveEntranceStrengthScene extends CaveEntranceStrengthBaseScene {
    succeeded: boolean;
    energy: number;
    render() {
        this.energy = this.state.character.energy;
        const texts: string[] = [];
        texts.push(`${this.state.character.name} decided to take brute-force approach with the door. ${this.mainText}`);
        texts.push(`${Chars.mark} energy -1`);
        this.energy -= 1;
        this.succeeded = 3 <= this.state.character.strength;
        texts.push(`${getSuccessFailureChar(this.succeeded)} need strength: ${strengthToText(3)}`);
        if (this.succeeded)
            texts.push(this.successText);
        else
            texts.push(this.failureText);
        return texts.map(t => this.text(t));
    }
    receiveChoice() {
        this.state.character.energy = this.energy;
        if (this.succeeded) {
            this.nextSceneCtor = CaveTraverseScene;
        } else {
            this.nextSceneCtor = PreCaveEntranceScene;
        }
    }
}

export class CaveEntranceEnhancedStrengthScene extends CaveEntranceStrengthBaseScene {
    succeeded: boolean;
    energy: number;
    render() {
        this.energy = this.state.character.energy;
        const texts: string[] = [];
        texts.push(`${this.state.character.name} casts a spell to temporarily enhance ${this.hisHer} strength. ${this.mainText}`);
        texts.push(`${Chars.mark} energy -2`);
        this.energy -= 2;
        // Note: with current character creation logic, this always succeeds
        this.succeeded = 3 <= this.state.character.strength + this.state.character.magic;
        texts.push(`${getSuccessFailureChar(this.succeeded)} need strength + magic: 3`);
        if (this.succeeded)
            texts.push(this.successText);
        else
            texts.push(this.failureText);
        return texts.map(t => this.text(t));
    }
    receiveChoice() {
        this.state.character.energy = this.energy;
        if (this.succeeded) {
            this.nextSceneCtor = CaveTraverseScene;
        } else {
            this.nextSceneCtor = PreCaveEntranceScene;
        }
    }
}

export class CaveEntranceMagicScene extends BaseScene {
    succeeded: boolean;
    energy: number;
    render() {
        this.energy = this.state.character.energy;
        this.succeeded = 3 <= this.state.character.magic;
        const texts: string[] = [];
        texts.push(`${this.state.character.name} casts a spell to pull the stone door open.`);
        texts.push(`${Chars.mark} energy -1`);
        this.energy -= 1;
        texts.push(`${getSuccessFailureChar(this.succeeded)} need magic: ${magicPowerToText(3)}`);
        if (this.succeeded)
            texts.push(`Slowly but surely, the door opens. Success!`);
        else
            texts.push(`But the spell is not strong enough. The door remains sealed. ${this.state.character.name} is out of breath from the effort.`);
        return texts.map(t => this.text(t));
    }
    receiveChoice() {
        this.state.character.energy = this.energy;
        if (this.succeeded) {
            this.nextSceneCtor = CaveTraverseScene;
        } else {
            this.nextSceneCtor = PreCaveEntranceScene;
        }
    }
}

enum StuckChoice {
    back = 'b',
    forward = 'f',
}

class CaveEntranceTunnelBaseScene extends BaseScene {
    get entranceReachedText() {
        return `After taking several turns left and right ${this.heShe} emerges into a dark cave.
            ${upperFirst(this.heShe)} conjures some light and sure enough sees the stone door on the right,
            this time from the inside.`;
    }
    get goStuckText() {
        return `It goes well at first.
            But then ${this.heShe} reaches a tight spot... When ${this.heShe} tries to go through,
            it turns out that the gap is not wide enough for ${this.himHer}. With ${this.hisHer}
            body pressed against the stone walls on both sides, should ${this.state.character.name} attempt to pull ${this.himselfHerself} through forcefully?
            ${upperFirst(this.heShe)} is not stuck yet, but ${this.heShe} might get stuck fast if ${this.heShe} tries that.`;
    }
}

export class CaveEntranceTunnelScene extends CaveEntranceTunnelBaseScene {
    succeeded: boolean;
    get stuckChoices() {
        return [new StoryChoice('go back', StuckChoice.back), new StoryChoice('go forward', StuckChoice.forward)];
    }
    render() {
        const texts: string[] = [];
        const choices: StoryChoice[] = [];
        this.succeeded = false;
        texts.push(`${this.state.character.name} decides to go through the narrow cleft.`);
        if (this.state.character.bodyType == BodyType.thin) {
            texts.push(`(${Chars.checkMark} thin) ${upperFirst(this.heShe)} goes through the whole path easily, even past the tightest spot.
                ${this.entranceReachedText}`);
            this.succeeded = true;
        }
        if (this.state.character.bodyType == BodyType.average) {
            texts.push(`(${Chars.checkMark} body type: average). When ${this.heShe} reaches the tightest spot
                it almost seems that there might be not enough space but ${this.heShe} manages to pass it fairly easily,
                barely avoiding touching the walls. ${this.entranceReachedText}`);
            this.succeeded = true;
        }
        if (this.state.character.bodyType == BodyType.chubby) {
            texts.push(`(${Chars.mark} body type: chubby). ${this.goStuckText}`);
            choices.push(...this.stuckChoices);
        }
        if (this.state.character.bodyType == BodyType.fat) {
            texts.push(`(${Chars.mark} body type: fat). ${this.goStuckText}`);
            choices.push(...this.stuckChoices);
        }
        return [
            ...texts.map(t => this.text(t)),
            ...choices
        ];
    }
    receiveChoice(choice: string) {
        if (this.succeeded) {
            this.nextSceneCtor = CaveTraverseScene;
        } else {
            switch (choice) {
                case StuckChoice.forward:
                    this.nextSceneCtor = CaveEntranceStuckScene;
                    break;
                case StuckChoice.back:
                    this.nextSceneCtor = CaveEntranceTunnelGoBackScene;
                    break;
            }
        }
    }
}

export class CaveEntranceTunnelGoBackScene extends BaseScene {
    render() {
        return [this.text(`${this.state.character.name} decided to turn back. ${upperFirst(this.heShe)} reached the main entrance without incident.`)];
    }
    receiveChoice() {
        this.nextSceneCtor = PreCaveEntranceScene;
    }
}

export class CaveEntranceStuckScene extends BaseScene {
    energy: number;
    succeeded: boolean;
    render() {
        this.energy = this.state.character.energy;
        this.succeeded = false;
        const elements: StoryElement[] = [];
        elements.push(this.text(`${this.state.character.name} tries to squeeze through the tight spot.`));
        if (this.state.character.bodyType == BodyType.chubby) {
            elements.push(
                this.text(`${Chars.checkMark} body type: chubby. With some effort,
                    ${this.state.character.name} managed to squeeze through successfully;
                    even though at one point ${this.heShe} almost got stuck for good.
                    ${this.hisHer} ${this.state.character.getTopClothingShortText()} is now somewhat dirty and slightly torn.`),
                this.text(`${Chars.mark} energy -1`)
            );
            this.energy -= 1;
            this.succeeded = true;
        } else if (this.state.character.bodyType == BodyType.fat) {
            elements.push(this.text(`${Chars.failMark} body type: fat.
                    ${this.state.character.name} tries to squeeze through the tight spot but ends up getting stuck fast.
                    Now ${this.heShe} can't move neither forward nor backward! It was a bad idea.`),
                this.text(`While ${this.state.character.name} contemplates solemnly what to do next,
                    ${this.heShe} hears a rattling sound. Soon a giant spider emerges from around the corner
                    and attacks ${this.himHer} sensing easy prey!
                    ${this.state.character.name} cannot fight back efficiently because ${this.heShe} is stuck.`));
        }
        return elements;
    }
    receiveChoice() {
        if (this.succeeded)
            this.nextSceneCtor = CaveTraverseScene;
        else
            this.nextSceneCtor = commonScenes.GameOverScene;
        if (this.nextSceneCtor != null)
            this.state.character.energy = this.energy;
    }
}

export class CaveTraverseScene extends BaseScene {
    renown: number;
    energy: number;
    render() {
        this.renown = this.state.renown;
        this.energy = this.state.character.energy;
        const elements: StoryElement[] = [];
        elements.push(this.text(`${this.state.character.name} traverses the cave carefully
            while holding a small magical light in ${this.hisHer} hand and trying to spot possible traps.`));
        elements.push(
            this.text(`You may gain score depending on how much energy you spent solving cave-entrance puzzle`),
            this.text(`${Chars.mark} energy: ${this.energy}`)
        );
        if (this.state.character.energy > 0) {
            elements.push(this.text(`${Chars.checkMark} score +${this.energy}`));
            this.renown += this.state.character.energy;
        } else {
            elements.push(this.text(`${Chars.mark} score: no bonus`))
        }
        if (this.energy <= 1)
            elements.push(this.text(`${this.state.character.name} stops to catch ${this.hisHer} breath.`));
        else
            elements.push(this.text(`${this.state.character.name} is still full of energy.`));
        if (this.state.character.energy <= 3)
            elements.push(this.text(`${Chars.mark} energy restored -> 3`));
        return elements;
    }
    receiveChoice() {
        this.nextSceneCtor = commonScenes.CaveSpiderScene;
        this.state.character.energy = 3;
        this.state.renown = this.renown;
    }
}

