import { BaseScene } from "../BaseScene";
import { StoryElement } from "../../story-engine/src/common/StoryElement";
import { strengthToText } from "../strength";
import { BodyType } from "../BodyType";
import { magicPowerToText } from "../magic";

export class StatusScene extends BaseScene {
    render() {
        const elements: StoryElement[] = [this.text('STATUS')];
        const state = this.state;
        if (state.character.name != null)
            elements.push(this.text('Name: ' + state.character.name));
        if (state.character.gender != null)
            elements.push(this.text('gender: ' + state.character.gender));
        if (state.character.bodyType != null)
            elements.push(this.text('body: ' + BodyType[state.character.bodyType]))
        if (state.character.strength != null)
            elements.push(this.text('strength: ' + strengthToText(state.character.strength)));
        if (state.character.magic != null)
            elements.push(this.text('magic: ' + magicPowerToText(state.character.magic)));
        elements.push(this.text('energy: ' + state.character.energy));
        elements.push(this.text('score: ' + state.renown));
        elements.push(...state.character.getClothingDescriptions().map(desc => this.text(desc)));
        return elements;
    }
    receiveChoice() {
        this.nextSceneCtor = this.getRequiredSceneCtor(this.state.returningSceneName);
    }
}