export function upperFirst(s: string): string {
    if (s.length > 0) {
        const firstLetter = s.charAt(0).toUpperCase();
        s = firstLetter + s.slice(1);
    }
    return s;
}

export function getLinearString(text: string): string {
    let output: string = '';
    let haveWhitespace = true;
    for (const c of text) {
        if (c != '\r' && c != '\n') {
            if (c == ' ') {
                if (!haveWhitespace) {
                    output += c;
                    haveWhitespace = true;
                }
            } else {
                output += c;
                haveWhitespace = false;
            }
        }
    }
    return output;
}