import { Component, h } from "preact";
import { StoryElement } from "../common/StoryElement";
import { StoryText } from "../common/StoryText";
import { Colors } from "./Colors";
import { StoryChoice } from "../common/StoryChoice";
import { UserReactionStoryElement } from "./UserReactionStoryElement";

class GuiStoryPanelProps {
    elements: StoryElement[];
}

export class GuiStoryPanel extends Component<GuiStoryPanelProps> {
    render() {
        return <div style={this.panelStyle}>
            {this.props.elements.map(element => this.renderElement(element))}
            {this.renderChoices()}
        </div>
    }
    panelStyle = {
        fontFamily: 'Consolas, monospace',
        border: '1px solid ' + Colors.solarizedBase01,
        backgroundColor: Colors.solarizedBase02,
        marginBottom: '4px',
        padding: '4px',
    }
    renderElement(element: StoryElement) {
        if (element instanceof StoryText) {
            const textElement = element as StoryText;
            return <div>{textElement.text}</div>;
        }
        if (element instanceof UserReactionStoryElement) {
            const e = element as UserReactionStoryElement;
            return <div>
                <span>
                    &gt;
                </span>&nbsp;
                <span style={{color: Colors.solarizedRed}}>
                    {e.text}
                </span>
                { e.subText != null
                    ? <div>
                        {e.subText}
                    </div>
                    : null}
            </div>;
        }
    }
    renderChoices() {
        const choices = this.props.elements.filter(e => e instanceof StoryChoice).map(e => e as StoryChoice);
        if (choices.length > 0) {
            return <ul style="margin-top: 4px; margin-bottom: 4px">
                {choices.map(choice => this.renderChoice(choice))}
            </ul>;
        } else
            return null;
    }
    renderChoice(choice: StoryChoice) {
        return <li>
            <span style={{color: Colors.solarizedRed}}>
                {choice.id}
            </span>:&nbsp;
            {choice.text}
        </li>;
    }
}